@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form-container {
    background: rgba(19, 19, 19, 0.7);
    width: 40%;
    margin: 50px 0;
    padding: 40px;
    border: white 1px solid;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

@media(max-width: 768px) {
    .form-container {
        width: 70%;
    }
}

@media(max-width: 425px) {
    .form-container {
        width: 90%;
        padding: 20px;
    }
}


.image-link-form {
    display: block;
    width: 100%;
    margin: 30px 0;
}
.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.bounding-box:hover {
  box-shadow: 0 0 0 3px #0c6297 inset;
  
}

  
.App {
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding-bottom: 20px;
  color: white;
}

.background-img {
  width: 100vw;
  overflow: hidden;
  position: fixed;
  z-index: -1;
}

.image-received {
  width: 500px;
}

@media(max-width: 768px) {
  .image-emoji {
    display: none;
  }

  .image-received{
    width: 98%;
  }

  .background-img {
    min-height: 100vh;
    max-height: 100%;
  }
}


